<template>

  <!--内存信息 -->
  <div>



    <div>
      <Row :gutter="10" style="margin-bottom: 20px;">
        <Col span="6">
        <Card>
          <p slot="title">CPU核心数</p>
          <p>{{localNodeInfo.totalCpus}}</p>
        </Card>
        </Col>
        <Col span="6">
        <Card>
          <p slot="title">系统运行时间</p>
          <p>{{localNodeInfo.nodeStartTime}}</p>
        </Card>
        </Col>
        <Col span="6">
        <Card>
          <p slot="title">IP</p>
          <p>{{localNodeInfo.addresses[2]}}</p>
        </Card>
        </Col>
        <Col span="6">
        <Card>
          <p slot="title">集群节点总数</p>
          <p>{{localNodeInfo.totalNodes}}</p>
        </Card>
        </Col>
      </Row>
      <Card>
        <p slot="title">CPU状态</p>
        <Row>
          <Col span="8">
          <Card>
            <div style="text-align:center;margin-left:10px;margin-right:10px;">
              <h3>
                平均CPU使用率
                <br>
                <i-circle :percent="localNodeInfo.averageCpuLoad * 100">
                  <span class="demo-Circle-inner" style="font-size:24px">{{localNodeInfo.averageCpuLoad * 100}}%</span>
                </i-circle>
              </h3>
            </div>
          </Card>
          </Col>
          <Col span="8">
          <Card>
            <div style="text-align:center;margin-left:10px;margin-right:10px;">
              <h3>
                当前CPU使用率
                <br>
                <i-circle :percent="localNodeInfo.currentCpuLoad * 100">
                  <span class="demo-Circle-inner" style="font-size:24px;">{{localNodeInfo.currentCpuLoad * 100}}%</span>
                </i-circle>
              </h3>
            </div>
          </Card>
          </Col>
          <Col span="8">
          <Card>
            <div style="text-align:center;margin-left:10px;margin-right:10px;">
              <h3>
                当前GC CPU使用率
                <br>
                <i-circle :percent="localNodeInfo.currentGcCpuLoad * 100">
                  <span class="demo-Circle-inner"
                    style="font-size:24px">{{localNodeInfo.currentGcCpuLoad * 100}}%</span>
                </i-circle>
              </h3>
            </div>
          </Card>
          </Col>
        </Row>
      </Card>
    </div>


  </div>
  </div>
</template>
<style scoped>
  .card-info {
    font-size: 20px;
  }
</style>

<script>

let config = require("../../config/config.json");

  export default {
    created() {
      this.loadData();
    },
    data() {
      return {
        localNodeInfo: {
          totalCpus: 4,
          addresses: ["0:0:0:0:0:0:0:1", "127.0.0.1", "192.168.0.105"],
          averageCpuLoad: 0.09,
          hostNames: ["DESKTOP-V0BBPV7"],
          nodeStartTime: 1552016853601,
          currentGcCpuLoad: 0,
          id: "9ffbe39b-ff82-4b9a-9efa-e09491c13066",
          currentCpuLoad: 0.0,
          version: {},
          local: true,
          totalNodes: 1
        }
      };
    },
    methods: {
      /**
      从服务器请求数据
      http://localhost:2580/dashboard/localNodeInfo


       */
      loadData() {
        let _this = this;
        this.axios
          .get(
            "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/localNodeInfo", {
              params: {},
              headers: {
                web_console_token: localStorage.getItem("web_console_token")
              }
            }
          )
          .then(response => {
            console.log(response.data);
            if (response.data.state == 1) {
              _this.localNodeInfo = response.data.data;
            } else {
              this.$Message.error("数据请求失败");
            }
          })
          .catch(response => {
            console.log(response);
          });
      }
    },
  };
</script>